@import url("https://fonts.googleapis.com/css2?family=Fauna+One&display=swap");

::-webkit-scrollbar-track {
  background-color: transparent;
}
:root {
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}
* {
  margin: 0px;
  padding: 0px;
}
body {
  font-family: "Fauna One", serif, -apple-system, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: #000000;
  font-size: 60px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  height: 8vh;
  position: sticky;
  top: 0px;
  z-index: 2;
}
.linkTab a :hover::before {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #f44336;
  filter: drop-shadow(0 5px 5px #ff1100);
}

.text {
  font-size: 4rem;
  padding: 10rem 0;
  color: #ffd700;
  letter-spacing: 6px;
  /* filter: drop-shadow(0px 5px 1px gold); */
}
.text_animate{
  white-space: nowrap;
  overflow: hidden;
  border-right: 1px solid #000;
  animation: typing 4s steps(40) 1, blink-caret 1s step-end 1;
}

@keyframes typing {
  from {
      width: 0;
  }
  to {
      width: 60%;
  }
}

@keyframes blink-caret {
  from, to {
      border-color: transparent;
  }
  50% {
      border-color: #000;
  }
  100%{
    border-right: none
  }
}

.drawer {
  display: none;
}

#about {
  padding-top: 6rem;
}
#about img {
  color: #1c2541;
  width: 30vw;
}

#skills {
  padding-top: 6rem;
  justify-content: center;
  align-items: center;
}
.show {
  background-color: red;
}

.skillbox {
  width: 130px;
  padding-top: 3px;
  margin: 10px;
  align-items: center;
  text-align: center;
}

#projects {
  padding-top: 6rem;
  justify-content: center;
  align-items: center;
}
.card_media img {
  width: 280px;
  border: 1px solid rgba(138, 138, 26, 0.644);
  border-radius: 10px;
}

#contacts {
  padding-top: 3rem;
  margin-top: 50px;
  overflow: hidden;
  justify-content: center;
}

.socialIcon {
  display: flex;
  padding: 2px;
  margin: 20px 0px;
  background: #f56403;
  background: green;
}
.socialIcon:hover {
  filter: drop-shadow(0 0 5px #f5d103);
  transition: 0.2s;
}

.Form {
  width: 500px;
  margin-bottom: 3rem;
}
@media screen and (max-width:900px) {
  #about img {
    width: 90vw;
  }
}

@media screen and (max-width: 600px) {
  .linkTab a {
    display: none;
  }
  .drawer {
    display: block;
    border: 0px;
    background: transparent;
    margin-right: 20px;
  }
  .text {
    font-size: 12vw;
    letter-spacing:normal;
  }

  #about {
    padding-top: 3rem;
    margin-bottom: 1rem;
  }
  #about img {
    width: 90vw;
  }
  #skills {
    padding-top: 0rem;
  }

  .skillbox {
    padding-top: 4px;
    margin: 6px;
    scroll-behavior: smooth;
  }

  .skillbox img {
    width: 60px;
  }
}

